import type { ViewName } from '../views';

/* ---------- Êï∞ÊçÆÈ°π ---------- */
export interface Item {
  id: string;
  title: string;
  content: string;
  type: 'task' | 'block';
  status?: string;
  category: string;
  tags: string[];
  recurrence: string;
  date?: string;
  created: number;
  modified: number;
  extra: Record<string, string | number | boolean>;
  header?: string;
  icon?: string;
  priority?: 'lowest' | 'low' | 'medium' | 'high' | 'highest';
  createdDate?: string;
  scheduledDate?: string;
  startDate?: string;
  dueDate?: string;
  doneDate?: string;
  cancelledDate?: string;
  filename?: string;
}

/* ---------- ‰ª™Ë°®ÁõòÊ®°ÂùóÈÖçÁΩÆ ---------- */
export interface ModuleConfig {
  /* Âü∫Êú¨ */
  view: ViewName;
  title: string;
  collapsed?: boolean;

  /* Êï∞ÊçÆËøáÊª§ / ÊéíÂ∫è */
  filters?: FilterRule[];
  sort?: SortRule[];

  /* ÂàÜÁªÑÔºàBlock / List / Excel Á≠âÈÄöÁî®Ôºâ*/
  group?: string;

  /* TableView ‰∏ìÁî® */
  rowField?: string;
  colField?: string;

  /* ÂÖ∂‰ªñ */
  props?: Record<string, any>;
  fields?: string[];
}

/* Filter / Sort */
export interface FilterRule {
  field: string;
  op: '=' | '!=' | 'includes' | 'regex' | '>' | '<';
  value: any;
}
export interface SortRule {
  field: string;
  dir: 'asc' | 'desc';
}

/* ---------- ‰ª™Ë°®Áõò ---------- */
export interface DashboardConfig {
  name: string;
  path?: string;
  tags?: string[];
  initialView?: string;
  initialDate?: string;
  modules: ModuleConfig[];
}

/* ---------- Ê†∏ÂøÉÂ≠óÊÆµ ---------- */
export const CORE_FIELDS = [
  'id','title','content','type','status','category','tags','recurrence',
  'date','header','icon','priority','createdDate','scheduledDate','startDate',
  'dueDate','doneDate','cancelledDate','created','modified','filename',
] as const;
export type CoreField = typeof CORE_FIELDS[number];

/* ---------- Â∑•ÂÖ∑ ---------- */
export function getAllFields(items: Item[]): string[] {
  const set = new Set<string>(CORE_FIELDS as unknown as string[]);
  for (const it of items) Object.keys(it.extra || {}).forEach(k => set.add('extra.' + k));
  return Array.from(set);
}
export function readField(item: Item, field: string): any {
  if (field.startsWith('extra.')) return item.extra?.[field.slice(6)];
  return (item as any)[field];
}
