// src/utils/templates.ts
import { EMOJI } from '../config/constants';
import { normalizeDateStr } from './date';

export function lastSegment(path: string) {
  return (path || '').split('/').filter(Boolean).pop() || path || '';
}

/** ÁîüÊàê‰ªªÂä°Ë°åÔºåÁ°Æ‰øù‰∏é parser ÂØπÈΩê */
export function makeTaskLine(opts: {
  prefix: '- [ ] ' | '- [x] ' | '- [-] ' | '';
  title: string;
  themePath?: string;         // Áî®‰∫é #Ê†áÁ≠æ
  icon?: string;              // ÊîæÂú®Ê†áÈ¢òÊúÄÂâçÁöÑ emoji
  due?: string;               // YYYY-MM-DD
  start?: string;             // YYYY-MM-DD
  scheduled?: string;         // YYYY-MM-DD
  createdISO?: string;        // YYYY-MM-DD
  timeHHMM?: string;          // HH:mm
  duration?: string | number; // ‰ªªÊÑè
  repeat?: 'day' | 'week' | 'month' | 'year' | null;
  markDate?: string;          // ÂÆåÊàê/ÂèñÊ∂àÊó∂ÁöÑÊó•Êúü YYYY-MM-DD
  statusEmoji?: string;       // ‚úÖ Êàñ ‚ùåÔºàÈÖçÂêà markDateÔºâ
}) {
  const tagStr = opts.themePath ? ` #${opts.themePath}` : '';
  const iconStr = opts.icon ? `${opts.icon} ` : '';
  const pieces: string[] = [];

  // ‰ªªÂä°‰∏ªÂπ≤
  pieces.push(`${opts.prefix}${iconStr}${opts.title}${tagStr}`.trim());

  // Ë°åÂÜÖ KV
  if (opts.timeHHMM) pieces.push(`(Êó∂Èó¥::${opts.timeHHMM})`);
  if (opts.duration !== undefined && opts.duration !== '')
    pieces.push(`(Êó∂Èïø::${String(opts.duration)})`);

  // Êó•Êúü emoji
  const addDate = (emoji: string, d?: string) => {
    if (d) pieces.push(`${emoji} ${normalizeDateStr(d)}`);
  };
  addDate(EMOJI.created, opts.createdISO);
  addDate(EMOJI.start, opts.start);
  addDate(EMOJI.scheduled, opts.scheduled);
  addDate(EMOJI.due, opts.due);

  // ÈáçÂ§ç
  if (opts.repeat) {
    pieces.push(`${EMOJI.repeat} every ${opts.repeat}${opts.repeat === 'day' ? '' : 's'}`);
  }

  // ÂÆåÊàê/ÂèñÊ∂àÊî∂Â∞æ
  if (opts.statusEmoji && opts.markDate) {
    pieces.push(`${opts.statusEmoji} ${normalizeDateStr(opts.markDate)}`);
  }

  return pieces.join(' ').trim();
}

/** ÁîüÊàêÂùóÔºàËÆ°Âàí/ÊÄªÁªì/ÊÄùËÄÉ/ÊâìÂç°Ôºâ */
export function makeBlock(opts: {
  category: string;           // ËÆ°Âàí/ÊÄªÁªì/ÊÄùËÄÉ/ÊâìÂç°
  dateISO?: string;
  themeLabel?: string;        // ‰∏ªÈ¢ò:: ÊñáÊú¨ÔºàÂ¶Ç È°∂Â±ÇÂ§ßÁ±ªÔºöÁîüÊ¥ª/ÁîµËÑë/‚Ä¶ ÁöÑÊòæÁ§∫ÂÄºÔºâ
  icon?: string;
  content: string;            // ÂèØÂ§öË°å
  tags?: string[];            // Ê†áÁ≠æ:: a, b
  extra?: Record<string, string | number | boolean>;
}) {
  const lines: string[] = ['<!-- start -->'];
  lines.push(`ÂàÜÁ±ª:: ${opts.category}`);
  if (opts.dateISO) lines.push(`Êó•Êúü:: ${normalizeDateStr(opts.dateISO)}`);
  if (opts.themeLabel) lines.push(`‰∏ªÈ¢ò:: ${opts.themeLabel}`);
  if (opts.icon) lines.push(`ÂõæÊ†á:: ${opts.icon}`);

  if (opts.tags && opts.tags.length) {
    lines.push(`Ê†áÁ≠æ:: ${opts.tags.join(', ')}`);
  }
  if (opts.extra) {
    Object.entries(opts.extra).forEach(([k, v]) => lines.push(`${k}:: ${String(v)}`));
  }

  // ÂÜÖÂÆπÂèØ‰ª•Â§öË°å
  const content = (opts.content || '').trim();
  if (content.includes('\n')) {
    lines.push('ÂÜÖÂÆπ:: ' + content.split('\n')[0]);
    lines.push(...content.split('\n').slice(1));
  } else {
    lines.push('ÂÜÖÂÆπ:: ' + content);
  }
  lines.push('<!-- end -->');
  return lines.join('\n');
}