// src/ui/modals/QuickBlockModal.tsx
/** @jsxImportSource preact */
import { App, Modal, Notice } from 'obsidian';
import { h } from 'preact';
import { render } from 'preact';
import { useMemo, useState } from 'preact/hooks';
import type ThinkPlugin from '../../main';
import { InputService } from '../../services/inputService';
import { makeBlock, lastSegment } from '../../utils/templates';

function todayISO() { return (window as any).moment().format('YYYY-MM-DD'); }

export class QuickBlockModal extends Modal {
  constructor(private plugin: ThinkPlugin) {
    super(plugin.app);
  }
  onOpen() { render(<BlockForm app={this.app} plugin={this.plugin} close={()=>this.close()}/>, this.contentEl); }
  onClose() { this.contentEl.empty(); }
}

function BlockForm({ app, plugin, close }: { app: App; plugin: ThinkPlugin; close: ()=>void; }) {
  const svc = new InputService(app, plugin);

  const [category, setCategory] = useState<'ËÆ°Âàí'|'ÊÄªÁªì'|'ÊÄùËÄÉ'>('ÊÄùËÄÉ');
  const [top, setTop] = useState<'ÁîüÊ¥ª'|'ÂÅ•Â∫∑'|'ÁîµËÑë'|'Â∑•‰Ωú'|'ÂÖ∂‰ªñ'>('ÁîüÊ¥ª');
  const themes = useMemo(()=>svc.listTaskThemesByTop(top, 'block', category), [top, category]);
  const [themePath, setThemePath] = useState(themes[0]?.path || top);
  const themeIcon = (themes.find(t=>t.path===themePath)?.icon) || '';

  const [dateISO, setDateISO] = useState(todayISO());
  const [content, setContent] = useState('');

  // ‰ªé inputSettings.base.blocks[category].fieldOptions ËØªÂèñÈÄâÈ°π
  const base = (plugin.inputSettings?.base?.blocks?.[category]) || {};
  const fieldOptions = base.fieldOptions || {};
  const [period, setPeriod] = useState<string>('');  // Âë®Êúü
  const [thoughtType, setThoughtType] = useState<string>(''); // ÊÄùËÄÉÂàÜÁ±ª

  const onSubmit = async () => {
    if (!content.trim()) { new Notice('ËØ∑Â°´ÂÜôÂÜÖÂÆπ'); return; }

    const tags = [themePath];               // Ê†áÁ≠æ:: ÁîµËÑë/ËÆ∞ÂΩïÁ≥ªÁªü
    const themeLabel = top;                 // ‰∏ªÈ¢ò:: È°∂Â±ÇÊòæÁ§∫ÔºàÂ¶Ç ÁîüÊ¥ª/ÁîµËÑë/‚Ä¶Ôºâ
    const extra: Record<string, any> = {};

    if (category === 'ËÆ°Âàí' || category === 'ÊÄªÁªì') {
      if (Array.isArray(fieldOptions['Âë®Êúü']) && period) extra['Âë®Êúü'] = period;
    } else if (category === 'ÊÄùËÄÉ') {
      if (Array.isArray(fieldOptions['ÂàÜÁ±ª']) && thoughtType) extra['ÂàÜÁ±ª'] = thoughtType;
    }

    const blockText = makeBlock({
      category,
      dateISO,
      themeLabel,
      icon: themeIcon,
      content,
      tags,
      extra,
    });

    try {
      await svc.writeBlock(themePath, category, null, blockText);
      new Notice('Â∑≤‰øùÂ≠ò');
      plugin.dataStore?.notifyChange?.();
      close();
    } catch (e: any) {
      new Notice('‰øùÂ≠òÂ§±Ë¥•Ôºö' + e.message);
    }
  };

  return (
    <div class="think-modal">
      <h3>Âø´ÈÄüÂΩïÂÖ• ¬∑ {category}</h3>

      <div class="setting-item"><div class="setting-item-name">Á±ªÂà´</div>
        <div class="setting-item-control">
          {(['ËÆ°Âàí','ÊÄªÁªì','ÊÄùËÄÉ'] as const).map(c=>(
            <label style="margin-right:10px;">
              <input type="radio" name="cat" checked={category===c} onChange={()=>{ setCategory(c); }} />
              <span style="margin-left:4px;">{c}</span>
            </label>
          ))}
        </div>
      </div>

      <div class="setting-item"><div class="setting-item-name">Â§ßÁ±ª</div>
        <div class="setting-item-control">
          {(['ÁîüÊ¥ª','ÂÅ•Â∫∑','ÁîµËÑë','Â∑•‰Ωú','ÂÖ∂‰ªñ'] as const).map(t=>(
            <label style="margin-right:8px;">
              <input type="radio" name="top" checked={top===t} onChange={()=>{ setTop(t); const ts = svc.listTaskThemesByTop(t,'block',category); setThemePath(ts[0]?.path || t); }}/>
              <span style="margin-left:4px;">{t}</span>
            </label>
          ))}
        </div>
      </div>

      <div class="setting-item"><div class="setting-item-name">‰∏ªÈ¢ò</div>
        <div class="setting-item-control">
          <select value={themePath} onChange={e=>setThemePath((e.target as HTMLSelectElement).value)} style="min-width:260px;">
            {svc.listTaskThemesByTop(top,'block',category).map(t=>(
              <option value={t.path}>{t.icon ? `${t.icon} ` : ''}{t.path}</option>
            ))}
          </select>
        </div>
      </div>

      {category!=='ÊÄùËÄÉ' && Array.isArray(fieldOptions['Âë®Êúü']) && (
        <div class="setting-item"><div class="setting-item-name">Âë®Êúü</div>
          <div class="setting-item-control">
            <select value={period} onChange={e=>setPeriod((e.target as HTMLSelectElement).value)}>
              <option value="">ÔºàÊó†Ôºâ</option>
              {fieldOptions['Âë®Êúü'].map((o: string)=> <option value={o}>{o}</option>)}
            </select>
          </div>
        </div>
      )}

      {category==='ÊÄùËÄÉ' && Array.isArray(fieldOptions['ÂàÜÁ±ª']) && (
        <div class="setting-item"><div class="setting-item-name">ÊÄùËÄÉÂàÜÁ±ª</div>
          <div class="setting-item-control">
            <select value={thoughtType} onChange={e=>setThoughtType((e.target as HTMLSelectElement).value)}>
              <option value="">ÔºàÊó†Ôºâ</option>
              {fieldOptions['ÂàÜÁ±ª'].map((o: string)=> <option value={o}>{o}</option>)}
            </select>
          </div>
        </div>
      )}

      <div class="setting-item"><div class="setting-item-name">Êó•Êúü</div>
        <div class="setting-item-control">
          <input type="date" value={dateISO} onChange={e=>setDateISO((e.target as HTMLInputElement).value)} />
        </div>
      </div>

      <div class="setting-item"><div class="setting-item-name">ÂÜÖÂÆπ</div>
        <div class="setting-item-control">
          <textarea rows={6} placeholder="ËæìÂÖ•ÂÜÖÂÆπ‚Ä¶" onInput={e=>setContent((e.target as HTMLTextAreaElement).value)} />
        </div>
      </div>

      <div style="display:flex;gap:8px;justify-content:flex-end;margin-top:8px;">
        <button class="mod-cta" onClick={onSubmit}>Êèê‰∫§ ‚Ü©Ô∏é</button>
        <button onClick={close}>ÂèñÊ∂à</button>
      </div>
    </div>
  );
}